.sectionArt {
    width: 100vw;
    height: 90vh;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    cursor: default;
}

.art_display {
    width: 812px;
    display: flex;
    justify-content: center;

    &_container {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
        margin: 20px;
        border: 2px solid #ffc4c4;
        border-radius: 15px;
        background-color: rgba(255, 255, 255, 0.64);
    }

    &_content {
        width: 100%;
        border: 6px solid #ffc4c4;
        border-radius: 20px;
        background-color: white;

        &_img {
            height: 200px;
            padding: 5px;
            border-radius: 10px;
        }
    }
}