.section_title {
    width: 50%;
    height: 4px;
    margin: 60px 0 40px 0;
    text-align: center;
    line-height: 1px;
    background:
        linear-gradient(to left,
            rgba(255, 255, 255, 1) 0%,
            rgba(86, 75, 75, 0) 50%,
            rgba(86, 75, 75, 0) 50%,
            rgba(255, 255, 255, 1) 100%) left bottom #564B4B no-repeat;

    &_text {
        font-size: 48px;
        font-family: 'Roboto Condensed', sans-serif;
        color: #564B4B;
    }
}

h span {
    background-image: linear-gradient(rgba(0, 0, 0, 0) 0%, white 40%, white 70%, rgba(0, 0, 0, 0));
    padding: 0 10px 0 10px;
    border-radius: 25px;
}

.diamond {
    width: 16px;
    height: 16px;
    display: inline-block;
    position: relative;
    vertical-align: top;
    margin-top: -5px;

    &:before {
        width: 16px;
        height: 16px;
        display: block;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        position: absolute;
        background-color: #564B4B;
        content: "";
        -webkit-transform: rotate(45deg);
        -webkit-transition: border-color 0.3s;
        -moz-transform: rotate(45deg);
        -moz-transition: border-color 0.3s;
        transform: rotate(45deg);

    }

    &_inner {
        width: 16px;
        height: 16px;
        display: inline-block;
        position: relative;
        margin-top: 5px;

        &:before {
            width: 8px;
            height: 8px;
            display: block;
            left: 4px;
            top: -1px;
            right: 0;
            bottom: 0;
            position: absolute;
            background-color: white;
            content: "";
            -webkit-transform: rotate(45deg);
            -webkit-transition: border-color 0.3s;
            -moz-transform: rotate(45deg);
            -moz-transition: border-color 0.3s;
            transform: rotate(45deg);
        }
    }
}

.top_arrow,
.top_arrow_more {
    width: 15px;
    height: 15px;
    position: fixed;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(-45deg);
    margin-left: 95%;
    margin-top: 30px;
    z-index: 10;
    cursor: pointer;
}

.top_arrow_more {
    bottom: 0;
    margin-bottom: 20px;
}

.space_divider {
    height: 7vh;
    display: flex;
    align-items: center;
}

.arrow_row {
    width: 15px;
    display: flex;
    position: absolute;
    margin-top: 0px;
}

.arrow_instruct {
    font-size: 18px;
    text-align: left;
    margin-left: 15px;
    bottom: 10px;
}

.arrow {
    width: 15px;
    height: 15px;
    position: relative;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(135deg);
    margin-left: 55px;
    margin-top: -10px;
}

.up_arrow {
    width: 15px;
    height: 15px;
    position: relative;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(-45deg);
    margin-left: 55px;
    cursor: pointer;
}

.up_arrow_row {
    width: 15px;
    margin-top: -40px;
}
