.sectionPrototype {
    width: 100vw;
    height: 90vh;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    cursor: default;
}

.prototypes_display {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    place-items: center;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3vw;
    padding: 10px;
    overflow: hidden;

    &_item {
        transition: all 0.2s ease-in-out;

        &:hover {
            filter: drop-shadow(0 0 2px #d69292);
            cursor: pointer;
        }
    }

    &_container {
        width: 300px;
        padding: 10px;
        border: 2px solid #ffc4c4;
        border-radius: 15px;
        background-color: rgba(255, 255, 255, 0.64);
        overflow: hidden;
    }

    &_content {
        width: min(100% - 10px);
        height: 350px;
        border: 6px solid #ffc4c4;
        border-radius: 20px;
        overflow: hidden;
        background-color: white;

        &_img {
            width: calc(100% - 10px);
            padding: 5px;
            border-radius: 20px;
        }

        &_description {
            width: calc(100% - 40px);
            height: 30%;
            display: flex;
            align-items: center;
            padding: 0 20px 0 20px;
            font-size: 14px;
            text-align: center;
            text-decoration: none;
            color: black;
            font-family: 'Lato', sans-serif;
        }

        &_name {
            width: calc(100% - 6px);
            margin: 10px 0 10px 0;
            padding: 5px 0 5px 0;
            font-size: 20px;
            text-align: center;
            font-family: 'Lato', sans-serif;
            background-color: #ffc4c4;
            border-radius: 5px;
            border: 3px solid white;
        }
    }
}

.prototypes_diamond {
    top: -27%;

    &:before {
        background-color: #ffc4c4;
    }
}

.sectionMore {
    width: 100vw;
    height: 100vh;

    &_nav {
        height: 8vh;
        display: flex;
        align-items: center;
        top: 0;
        position: sticky;
        background-color: white;
        box-shadow: 0 10px 10px rgba(0, 0, 0, .1);
        z-index: 10;

        &_back {
            display: inline-flex;
            align-items: center;
            text-align: left;
            margin-left: 40px;

            &_arrow {
                width: 15px;
                height: 15px;
                border-right: 3px solid black;
                border-top: 3px solid black;
                transform: rotate(-135deg);
                margin: 0px 20px 0px 0px;
                z-index: 10;
            }

            &:hover {
                font-weight: bold;
                cursor: pointer;
            }
        }
    }
}

.case_study_intro {
    width: 100%;
    height: 60vh;
    display: inline-flex;
    align-items: center;
    padding: 50px 0px 30px 0px;
    background-image: linear-gradient(to bottom, #ffc4c4, transparent);
    //background-color: #ffc4c4;

    &_details {
        width: max(45vw, 600px);
        min-width: 400px;
        margin: 10px 30px 0px 10px;
        display: inline-flex;
        flex-direction: column;
        text-align: left;

        &_title {
            font-size: 4vw;
            line-height: 1;
            font-family: 'Lato', sans-serif;
            font-weight: 700;
        }

        &_sum {
            line-height: 2;
        }
    }

    &_img {
        height: 100%;
        padding: 30px;
        border-radius: 50px;
    }
}

.case_study {
    width: 100%;
    display: flex;
    justify-content: left;
    align-items: flex-start;
    text-align: left;
    overflow: visible;
    //background-size: 32px 32px;
    //background-image: radial-gradient(circle, #ffc4c4 2px, rgba(0, 0, 0, 0) 1px);
    //background-size: 40px 40px;
    //background-image:
    //    linear-gradient(to right, rgba(255, 196, 196, 0.5) 1px, transparent 1px),
    //    linear-gradient(to bottom, rgba(255, 196, 196, 0.5) 1px, transparent 1px);

    &_section {
        width: 80vw;
        min-width: 800px;
        border-left: 5px solid #d97c7c;
        margin: 50px 0px 50px 30px;
        padding: 15px 0px 0px 30px;

        &_container {
            display: inline-block;
        }

        &_header {
            font-size: 40px;
            line-height: 1;
            font-family: 'Lato', sans-serif;
            font-weight: 700;
        }

        &_description {
            line-height: 2;
            width: 90%;
        }

        &_imageblock {
            display: flex;
            justify-content: center;
        }

        &_grid {
            display: grid;
            justify-items: center;
            align-items: center;
            grid-template-columns: 1fr 1fr;
            margin: 30px 0 30px 0;

            &_img {
                width: 90%;
                margin-left: 5%;

                &_before {
                    border: 1px solid grey;
                }

                &_after {
                    padding: 5px;
                    border: 1px solid grey;
                }
            }
        }
    }

    &_nav {
        width: 20vw;
        min-width: 200px;
        height: 100vh;
        display: inline-flex;
        flex-direction: column;
        justify-content: center;
        margin: 0px 20px 0px 40px;
        position: sticky;
        position: -webkit-sticky;
        top: 0;
        color: #d97c7c;
        line-height: 2;

        &_link {
            &:hover {
                cursor: pointer;
                text-decoration: underline;
                text-underline-offset: 4px;
            }
        }
    }
}

